
import React,{useState,useEffect} from 'react'
import articalContent from './article-content'
import ArticleList from '../components/ArticleList'
import PageNotFound from './PageNotFound'
import CommentsList from '../components/CommentsList'
import UpvoteSection from '../components/UpvoteSection'
import AddCommentForm from '../components/AddCommentForm'

const ArticlePage = ({match}) =>{
    const name = match.params.name;
    const articlee = articalContent.find(article => article.name===name);

    const[articleInfo,setArticleInfo] = useState({upvotes:0,comments:[]}); //hooks

    useEffect(()=>{ //hooks
        const fetchData = async() =>{
            const result = await fetch(`/api/articles/${name}`);
            const body = await result.json();
            setArticleInfo(body);
            console.log(body);
        }

        fetchData();
        
    },[name]); //[] for if array values are set to this then reload
    
    
    if(!articlee){
        return(
            <PageNotFound />
        )
    }
    else
    {   console.log("articleInfo");
        console.log(articleInfo);
        const otherArticles = articalContent.filter(article => article.name !== name)
        return(
            <>
            <h1>{articlee.title}</h1>   
            <UpvoteSection articleName={name} upvotes={articleInfo.upvotes} setArticleInfo={setArticleInfo}/>
            <br/>
            {articlee.content.map(
                (subcontent,key) =>(
                <p1 key  = {key}>{subcontent}</p1>
                )
            )}
            
            <CommentsList comments={articleInfo.comments}/>
            <AddCommentForm articleName={name}  setArticleInfo={setArticleInfo}/>
            <h2>Other articles</h2>
            <ArticleList artical = {otherArticles}/>   
            </>
    )}
   }
export default ArticlePage;